  /* --- Lightbox --- */

  .sby_lightboxOverlay {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9999;
    background-color: black;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
    opacity: 0.9;
    display: none;
  }

  .sby_lightbox {
    position: absolute;
    left: 0;
    width: 100%;
    z-index: 10000;
    line-height: 0;
    font-weight: normal;
    overflow-x: auto;
    height: 100%;
  }

  .sby_lightbox .sby_lb-header {
    padding-top: 80px;
  }

  .sby_lightbox .sby_lb-image {
    display: block;
    height: auto;
    max-width: inherit;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    border-radius: 3px;
  }

  .sby_lightbox a:hover,
  .sby_lightbox a:focus,
  .sby_lightbox a:active {
    outline: none;
  }

  .sby_lightbox a img {
    border: none;
  }

  .sby_lb-outerContainer {
    position: relative;
    background-color: #000;
    *zoom: 1;
    width: 250px;
    height: 250px;
    margin: 0 auto 0 auto;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  .sby_lb-outerContainer .sby_lb-close {
    position: absolute;
    top: -40px;
    right: 0px;
    width: 30px;
    height: 30px;
    background: url(../img/close.png) top right no-repeat;
    text-align: right;
    outline: none;
    cursor: pointer;
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
    opacity: 0.7;
    border: 0;
    padding: 0;
    -webkit-transition: opacity 0.2s;
    -moz-transition: opacity 0.2s;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
  }

  .sby_lb-outerContainer:after {
    content: "";
    display: table;
    clear: both;
  }

  .sby_lb-container {
    position: relative;
    padding: 4px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    bottom: 0;
    right: 0;

    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    border-radius: 3px;
  }

  .sby_lb-loader {
    position: absolute;
    top: 43%;
    left: 0;
    height: 25%;
    width: 100%;
    text-align: center;
    line-height: 0;
  }

  .sby_lb-cancel {
    display: block;
    width: 32px;
    height: 32px;
    margin: 0 auto;
    background-color: #666;
    background-color: rgba(255, 255, 255, 0.5);
    border: 0;
    padding: 0;
    border-radius: 100%;
    -webkit-animation: ctf-sk-scaleout 1s infinite ease-in-out;
    animation: ctf-sk-scaleout 1s infinite ease-in-out;
  }

  .sby_lb-nav {
    /*position: absolute;*/
    top: 0;
    left: 0;
    /*height: 100%;*/
    width: 100%;
    z-index: 10;
    /*pointer-events: none;*/
  }

  .sby_lb-container > .nav {
    left: 0;
  }

  .sby_lb-nav a {
    position: absolute;
    z-index: 100;
    top: 0;
    height: 90%;
    outline: none;
    background-image: url("data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==");
  }

  /* Arrows */
  .sby_lb-prev,
  .sby_lb-next {
    height: 100%;
    cursor: pointer;
    display: block;
  }

  .sby_lb-nav a.sby_lb-prev {
    /*width: 30%;*/
    width: 50px;
    left: -70px;
    padding-left: 10px;
    padding-right: 10px;
    float: left;

    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0.5);
    opacity: 0.5;
    -webkit-transition: opacity 0.2s;
    -moz-transition: opacity 0.2;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
  }

  .sby_lb-nav a.sby_lb-prev:hover {
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
    opacity: 1;
  }

  .sby_lb-nav a.sby_lb-next {
    /*width: 30%;*/
    width: 50px;
    right: -70px;
    padding-left: 10px;
    padding-right: 10px;
    float: right;

    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0.5);
    opacity: 0.5;
    -webkit-transition: opacity 0.2s;
    -moz-transition: opacity 0.2s;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
